/**
 * Demo6
 */

class Demo6 {

  constructor() {
    this.toggler = document.querySelector('#hamburger')
    this.opnendName = '-opened'
    this.opened = false
  }

  init() {
    if (!this.toggler) return
    this.attachEvent()
  }

  attachEvent() {
    this.toggler.addEventListener('click', e => {
      this.opened = !this.opened
      this.toggleState()
    })
  }

  toggleState() {
    // 
    if (this.opened) {
      this.toggler.classList.add(this.opnendName)
    } else {
      this.toggler.classList.remove(this.opnendName)
    }

    // aria
    if (this.opened) {
      this.toggler.setAttribute('aria-label', '')
    } else {
      this.toggler.setAttribute('aria-label', '')
    }
  }
}

export default Demo6



